@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,500;0,600;0,700;0,800;0,900;1,400;1,500&family=Poppins:wght@200;300;400;500;600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Source+Serif+Pro:wght@400;600;700&display=swap");

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

body {
  font-family: "Poppins", sans-serif;
  position: relative;
}

.active {
  right: 0;
}

.navPhone {
  right: -15rem;
}

.row > * {
  padding: 0;
}

.swiper-pagination-bullet {
  width: 1.3rem !important;
  height: 1.3rem !important;
  font-size: 1rem !important;
  background-color: orange;
  color: #fff;
}

.swiper-pagination {
  bottom: 2rem !important;
}

.bg {
  position: absolute;
  bottom: -25rem;
  left: 0;
  width: 100%;
}
.sticky {
  background-color: rgb(242, 164, 18);
  box-shadow: 0.1rem 0.1rem 0.5rem rgba(0, 0, 0, 0.3);
}

.sticky .links .link {
  color: #fff !important;
}

.sticky .links .link.active:before {
  background-color: #fff !important;
}

.blur {
  transition: 0.6s;
}

.navbar {
  background: rgba(0, 0, 0, 0.5);
  padding: 0 0 0 0;
}
.courseWrapper {
  margin-bottom: 7rem;
}

.container-fluid {
  padding: 0 10rem;
}

@media (max-width: 1700px) {
  .container-fluid {
    padding: 0 6rem;
  }
}
@media (max-width: 1600px) {
  .container-fluid {
    padding: 0 3rem;
  }
}
@media (max-width: 1400px) {
  .container-fluid {
    padding: 0 1rem;
  }
}

.swiper {
  -webkit-user-select: auto !important;
  -moz-user-select: auto !important;
  -ms-user-select: auto !important;
  user-select: auto !important;
}

[data-rmiz-wrap="visible"],
[data-rmiz-wrap="hidden"] {
  display: block;
  width: 100%;
  height: inherit;
}

.swiper-button-next:after,
.swiper-button-prev:after {
  font-size: 2rem;
}

.swiper-button-next,
.swiper-rtl .swiper-button-prev {
  top: 2rem;
}

.swiper-button-prev,
.swiper-rtl .swiper-button-prev{
  top: 2rem;
}

.swiper{
  position: relative !important;
}

.certificateHeader {
  position: absolute;
  top: 1.3rem;
  width: max-content;
  font-size: 1.4rem;
  left: 50%;
  transform: translateX(-50%);
  display: inline-block;
}

@media (max-width: 1088px) {
  .certificateHeader{
    font-size: 1.2rem;
  }
}
@media (max-width: 992px) {
  .certificateHeader{
    font-size: 1rem;
  }
}
@media (max-width: 576px) {
  .certificateHeader{
    font-size: 0.8rem;
  }
}



